:root {
  --num-step: 5px;
  --num0: calc(var(--num-step) * 0);
  --num1: calc(var(--num-step) * 1);
  --num2: calc(var(--num-step) * 2);
  --num3: calc(var(--num-step) * 3);
  --num4: calc(var(--num-step) * 4);
  --num5: calc(var(--num-step) * 5);
}
