.button {
  flex: 0 0 auto;
  height: 50px;

  font-size: 14px;
  padding: 0 var(--num3);

  box-sizing: border-box;
  border: 2px solid rgba(0, 0, 0, 0.7);
  border-radius: 5px;
  background: #bdb58e;

  text-transform: capitalize;
  cursor: pointer;
}

.list {
  overflow-y: auto;
  border: 2px solid #00000050;

  background: rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.list-item {
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.2);
  border: 1px solid #000000;
  border-radius: 2px;
}

