body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

a,
a:visited {
  color: #72d8ff;
}

.h-100 {
  height: 100%;
}

.w-100 {
  width: 100%;
}

.cursor-pointer {
  cursor: pointer;
}

.position-relative {
  position: relative;
}

.text-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.form {
  display: flex;
  flex-direction: column;

  padding: 20px;

  /* width: 320px; */

  /* card/bg */
  background: #645c51;
  color: white;
  /* input/label-bg */
  border: 2px solid rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  /* card/shadow */
  box-shadow: 0px 0px 6px #000000;
  border-radius: 10px;
}

.version-holder {
  position: absolute;
  bottom: 0;
  right: 0;

  text-align: right;

  color: white;
}

.input {
  --color-default: white;
  --color-blur: rgba(255, 255, 255, 0.6);
  --color-focus: #f1c857;
  --color-error: #ff4f4f;

  width: 280px;
}

.input-body {
  height: 56px;

  background: rgba(0, 0, 0, 0.2);
  border-radius: 5px 5px 0px 0px;
}

.input-icon {
  color: var(--color-blur);
}

.error-icon {
  color: var(--color-error);
}

.input hr {
  border: none;
  height: 2px;

  background-color: var(--color-blur);
}

.input-label {
  position: absolute;
  font-size: 12px;
  line-height: 16px;

  display: flex;
  align-items: flex-end;
  text-transform: capitalize;

  color: var(--color-default);
}

.input-value {
  font-size: 16px;
  color: var(--color-default);
  border: none;
  background: none;
  outline: none;
  padding: 0;
  height: 20px;
}

.input-footer {
  height: 20px;
  font-size: 12px;
  min-width: 0;
}

.helper-text {
  color: var(--color-blur);
}

.error-text {
  color: var(--color-error);
}

/* BLUR */
.input .input-label {
  color: var(--color-blur);
}

.input hr,
.input-footer {
  -webkit-transition: 150ms ease-in-out all;
  transition: 150ms ease-in-out all;
}

/* EMPTY */
.input .input-label {
  -webkit-transform: translate(0, -100%);
          transform: translate(0, -100%);
  font-size: 12px;
  -webkit-transition: 150ms ease-in-out all;
  transition: 150ms ease-in-out all;
}

.input.is-empty:not(.is-focused) .input-label {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  font-size: 16px;
}
/* FOCUS */

.input.is-focused hr {
  background-color: var(--color-focus);
}

.input.is-focused .input-label {
  color: var(--color-focus);
}

/* ERROR */

.input.has-error .input-label {
  color: var(--color-error);
}

.input.has-error hr {
  background-color: var(--color-error);
}

.status {
  position: absolute;
  top: 0;
  right: 0;

  color: white;
}

.app {
  text-align: center;

  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url(/pg-web/static/media/pg-background.b5f18203.png);

  -webkit-transition: all ease-in-out 300ms;

  transition: all ease-in-out 300ms;

  background-color: #696969;
}

.app.is-online {
  background-color: #778b44;
}

.app-logo {
  height: 40vmin;
  pointer-events: none;
}

.is-pulse {
  -webkit-animation: pulse infinite 1s ease-in-out alternate;
          animation: pulse infinite 1s ease-in-out alternate;
}

@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    opacity: 0.8;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    opacity: 0.8;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.rooms.form {
  max-height: 80vh;
  overflow: hidden;
}

.player-list,
.room-list {
  width: 320px;
}

